<template>
  <div class="container" v-loading="isLoading">
    <div class="App">
      <el-card class="ProjectCard">

        <div style="display: flex; justify-content: end; align-items: center;">
          <el-button @click="translateDlgVisible = true" type="primary">Request Translate</el-button>
          <el-button @click="dialogVisible = true" type="primary">Add String</el-button>
        </div>

        <div style="display: flex; justify-content: space-between; align-items: center; background-color: #f8f9fa">
          <span>All Strings</span>

          <div>
            <el-select v-model="languagesSelected" filterable>
              <el-option
                  v-for="item in languagesOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
            <i class="icon el-icon-download"/>
          </div>
        </div>

        <el-table
            :data="strings"
            stripe
            empty-text="No Data"
            style="width: 100%">

          <el-table-column
              prop="transId"
              label="ID">
          </el-table-column>

          <el-table-column
              prop="transKey"
              label="Key">
          </el-table-column>


          <el-table-column
              prop="context"
              label="Context">
          </el-table-column>

          <el-table-column v-for="(item, i) in languages" :key="i"
                           :label="getLanguageLabel(item)"
                           show-overflow-tooltip>
            <template slot-scope="scope">
              {{scope.row[item]}}
            </template>
          </el-table-column>

          <el-table-column
              fixed="right"
              label="Operate"
              width="100">
            <template slot-scope="scope">
              <el-button @click="handleEditString(scope.row)" type="text" size="small">Edit</el-button>
              <el-button @click="showDeleteStringDlg(scope.row)" type="text" size="small">Delete</el-button>
            </template>
          </el-table-column>

        </el-table>


      </el-card>
    </div>

    <el-dialog
        title="Add String"
        :visible.sync="dialogVisible"
        width="30%">

      <el-form ref="stringForm" :model="stringForm" :rules="rules">
        <el-form-item label="Key" prop="projectName">
          <el-input v-model="stringForm.transKey"></el-input>
        </el-form-item>
        <el-form-item label="Context" prop="languages">
          <el-input v-model="stringForm.transContext"></el-input>
        </el-form-item>
        <el-form-item label="Name(English)" prop="languages">
          <el-input v-model="stringForm.en"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">Cancel</el-button>
              <el-button type="primary" @click="handleAddString">Add</el-button>
            </span>
    </el-dialog>


    <el-dialog
        title="Edit String"
        :visible.sync="isUpdateStringEmpty"
        width="30%">

      <el-form ref="editStringForm" :model="editStringForm" :rules="rules">
        <el-form-item label="Key" prop="transKey">
          <el-input v-model="editStringForm.transKey"></el-input>
        </el-form-item>
        <el-form-item label="Context" prop="context">
          <el-input v-model="editStringForm.transContext"></el-input>
        </el-form-item>
        <el-form-item label="Name(English)" prop="EN">
          <el-input v-model="editStringForm.en"></el-input>
        </el-form-item>
        <el-form-item label="Value" prop="value">
          <el-input v-model="editStringForm[languages[languages.length - 1]]"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">

              <el-button @click="handleTranslateStrings">Translate</el-button>
              <el-button @click="editStringForm = {}">Cancel</el-button>
              <el-button type="primary" @click="handleUpdateString">Update</el-button>
            </span>
    </el-dialog>

    <el-dialog title="Request Translate" :visible.sync="translateDlgVisible" width="30%">

      <el-form :model="requestTransFrom">
        <el-form-item label="Target Language" label-width="120px">
          <el-select v-model="requestTransFrom.targetLanguage" placeholder="">

            <el-option v-for="item in languages" :label="getLanguageLabel(item)" :value="getLanguageValue(item)"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
              <el-button @click="translateDlgVisible = false">Cancel</el-button>
              <el-button type="primary" @click="handleRequestTranslate">Send</el-button>
      </span>
    </el-dialog>

    <el-dialog
        title="Hint"
        :visible.sync="isDeleteStringEmpty"
        width="30%">
      <span>Are you sure delete {{deleteStringData.transKey}}? </span>
      <span slot="footer" class="dialog-footer">
                <el-button @click="deleteStringData = {}">Cancel</el-button>
                <el-button type="primary" @click="handleDeleteString">Confirm</el-button>
            </span>
    </el-dialog>

  </div>

</template>

<script>
import {addString, updateString, getStrings, deleteString, requestAiTranslate, requestTranslateSingle} from '../../api'
import languages from "@/store/languages";

export default {
  data() {
    return {
      isLoading: true,
      dialogVisible: false,
      translateDlgVisible: false,
      strings: [],
      stringForm: {
        projectId: this.$route.params.id,
        transKey: '',
        transContext: '',
        en: ''
      },
      editStringForm: {},
      requestTransFrom: {
        targetLanguage: ''
      },
      rules: {
        transKey: [{required: true}]
      },
      languages: [],
      deleteStringData: {},
      languagesOptions:[],
      languagesSelected: "EN"
    }
  },
  mounted() {
    this.refreshPage()
  },
  computed: {
    projectData() {
      return this.$store.state.tab.projectData
    },
    getColumnLanguage(data) {
      console.log(data)
      return this.$store.state.allLanguages[data]
    },
    isDeleteStringEmpty() {
      return this.deleteStringData.transId != null
    },
    isUpdateStringEmpty() {
      return this.editStringForm.transId != null
    }
  },
  watch: {
    projectData(newVal, oldVal) {
     if (this.$route.params.id == this.$store.state.tab.projectData.projectId) {
        this.refreshPage()
      }
    },
    languagesSelected(newVal, oldVal) {
      if (newVal != oldVal) {
        this.refreshStrings()
      }
    }
  },
  methods: {
    handleEditString(row) {
      this.editStringForm = row
    },
    refreshPage() {
      // 构建语言选择列表
      const projectData = this.$store.state.tab.projectData;
      if (this.$route.params.id != projectData.projectId) {
        // 没有基础数据，暂时不处理
        return
      }

      const set = []
      projectData.languages.split(",").forEach((item) => {
        set.push(item.trim())
      });

      Object.keys(languages.state.allLanguages).forEach((item) => {
        if (set.includes(languages.state.languageKey[item])) {
          this.languagesOptions.push({
            label: languages.state.allLanguages[item],
            value: languages.state.languageKey[item],
          })
        }
      })

      this.refreshStrings()
    },
    refreshStrings() {
      // 获取选择语言下的翻译

      let language = "EN, " + this.languagesSelected

      const request = {
        "projectId": this.$route.params.id,
        "languages": language,
        "pageSize": 20, "pageNum": 0
      };

      getStrings(request).then((data) => {
        console.log(data)
        this.strings = data.data.strings
        this.languages = data.data.languageKey
        this.isLoading = false
      })
    },
    showDeleteStringDlg(row) {
      this.deleteStringData = row
    },
    handleDeleteString(row) {
      const request = {
        "transId": this.deleteStringData.transId
      }
      console.log(row)
      deleteString(request).then((data) => {
        if (data.code == 200) {

        }
      })
    },
    getLanguageLabel(name) {
      return languages.state.allLanguages[name]
    },
    getLanguageValue(name) {
      return languages.state.languageKey[name]
    },
    handleRequestTranslate() {
      const request = {
        "projectId": this.$route.params.id,
        "targetLanguage": this.requestTransFrom.targetLanguage
      }
      console.log(request)
      requestAiTranslate(request).then((data) => {
        if (data.code == 200) {
          this.translateDlgVisible = false
        }
      })
    },
    handleAddString() {
      this.$refs.stringForm.validate((valid) => {
        console.log(valid, "valid")
        if (valid) {
          this.dialogVisible = false
          addString(this.stringForm).then((data) => {
            if (data.code == 200) {
              this.strings.add(data.data)
            }
          })
        }
      })
    },
    handleUpdateString() {
      updateString(this.editStringForm).then((data) => {
        if (data.code == 200) {
          this.editStringForm = {}
        }
      })
    },
    handleTranslateStrings() {
      requestTranslateSingle(this.editStringForm).then((data) => {
        if (data.code == 200) {
          this.editStringForm = {}
        }
      })
    }
  }
}
</script>


<style lang="less" scoped>

.icon {
  cursor: pointer;
  float: right;
  padding: 12px;
  margin: 5px;
}

</style>